<style scoped>
hr {
  margin: 10px 0px;
}
</style>
<template>
  <div class="container">
    <div class="row">
      <div class="large-3 medium-3 small-3 cell">
        <h1>Converti ordini in pdf</h1>
        <div class="form-check">
          <input
            class="custom-file-input"
            type="file"
            id="files"
            ref="files"
            v-on:change="handleFileUploads()"
          />
          <label class="custom-file-label" for="inputGroupFile01"
            >Choose file</label
          >
        </div>
      </div>
    </div>
    <br />
    <div class="row">
      <div class="medium-3">
        <button
          class="btn btn-success"
          type="button"
          v-on:click="submitFiles()"
        >
          Carica
        </button>
      </div>
    </div>
    <div class="row">
      <div class="large-12 medium-12 small-12 cell">
        <p>{{ message }}</p>
      </div>
      <hr />
    </div>
    <div class="row">
      <div class="large-12 medium-12 small-12 cell">
        <a class="btn btn-primary" :href="href" download target="_blank"
          >Download</a
        >
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      files: "",
      href: "#",
      message: "",
    };
  },
  methods: {
    submitFiles() {
      let formData = new FormData();
      console.log(this.apiURL + "/ordini/upload");
      formData.append("files", this.files);
      var self = this;
      axios
        .post(
          // "http://archicons.archivium-srl.it/ArchiHub/procedures/varie/p/nsoToPDF.php",
          this.apiURL + "/ordini/upload",
          formData
        )
        .then(function () {
          self.message = "file caricato con successo";
          console.log("SUCCESS!!");
          // alert('caricato con successo!');
          self.href =
            "http://archicons.archivium-srl.it/ArchiHub/procedures/varie/p/ordini/" +
            self.files.name +
            ".pdf";
          // console.log(self.href);
        })
        .catch(function (r) {
          console.log(r);
          console.log("FAILURE!!");
        });
    },

    /*
        Handles a change on the file upload
      */
    handleFileUploads() {
      this.files = this.$refs.files.files[0];
    },
  },
};
</script>